import { BigNumber, utils, constants } from 'ethers'

const { formatEther } = utils
const { Zero } = constants

export const formatFloorEtherBalance = (balance = Zero, maxDecimalDigits = 4): string => {
  const modulo = BigNumber.from(10).pow(Math.max(0, 18 - maxDecimalDigits))
  return formatEther(balance.sub(balance.mod(modulo)))
}
